.sf-statistics {
  font-family: Lato, Helvetica, Times, serif;
  text-shadow: -2px 1px rgba(black, 0.1);
  text-transform: uppercase;
  font-size: 1.95ex;
  font-weight: 300;
  letter-spacing: 3.5px;
  
}

.stats-box {
  display: inline-block;
  margin: 0;
  font-size: 160%;
}

.stats-text {
  font-weight: bold;
}

.sf-stats {
  text-align: center;
  padding-bottom: 25px;
}

.timer {
  font-weight: 900;
  font-size: 3em;
}

.color-white {
  color: rgb(255, 255, 255);
}

.color-green {
  color: rgb(153, 230, 153);
}

.color-red {
  color: rgb(230, 153, 153);
}