.sf-logo {
  border-radius: 20px;
  text-align: center;
  color: white;
  margin-bottom: 30px;
}

.sf-logo h1 {
  margin: 1.414em 0 0.5em;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: -.45rem;
  margin-top: 0;
  font-size: 6.853em;
}

.sf-logo span {
  display: inline-block;
  font-weight: normal;
  color: #FF4056;
}

.sf-logo-container {
  margin-top: 50px;
  margin-bottom: 50px;
}


@media screen and (min-width: 781px) and (max-width: 1199px) {
  .sf-logo h1 {
    font-size: 3.853em;
  }
}

@media screen and (min-width: 540px) and (max-width: 780px) {
  .sf-logo h1 {
    font-size: 3.853em;
  }
}

/* For Tablets */
@media screen and (max-width: 539px) {
  .sf-logo h1 {
    font-size: 3.853em;
  }
}