@import url(https://fonts.googleapis.com/css?family=Lato:300|Montserrat:700,400);
body, html {
  margin: 0;
  padding: 0;
  font-family: Montserrat, Helvetica, Times, serif;
  text-rendering: optimizeLegibility;

  background: #2A2C39;
  min-height: 100%;
}

body {
  height:100vh;
}
.sf-logo {
  border-radius: 20px;
  text-align: center;
  color: white;
  margin-bottom: 30px;
}

.sf-logo h1 {
  margin: 1.414em 0 0.5em;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: -.45rem;
  margin-top: 0;
  font-size: 6.853em;
}

.sf-logo span {
  display: inline-block;
  font-weight: normal;
  color: #FF4056;
}

.sf-logo-container {
  margin-top: 50px;
  margin-bottom: 50px;
}


@media screen and (min-width: 781px) and (max-width: 1199px) {
  .sf-logo h1 {
    font-size: 3.853em;
  }
}

@media screen and (min-width: 540px) and (max-width: 780px) {
  .sf-logo h1 {
    font-size: 3.853em;
  }
}

/* For Tablets */
@media screen and (max-width: 539px) {
  .sf-logo h1 {
    font-size: 3.853em;
  }
}
.navbar-toggler {
    float: right;
  }
  
  .navbar {
    min-height: 80px;
    background-color:transparent !important;
      border-color:transparent;
      background-image:none;
      box-shadow:none;
  }
  
  .navbar-brand span {
    color: #FF4056; 
  }
  
.sf-statistics {
  font-family: Lato, Helvetica, Times, serif;
  text-shadow: -2px 1px rgba(black, 0.1);
  text-transform: uppercase;
  font-size: 1.95ex;
  font-weight: 300;
  letter-spacing: 3.5px;
  
}

.stats-box {
  display: inline-block;
  margin: 0;
  font-size: 160%;
}

.stats-text {
  font-weight: bold;
}

.sf-stats {
  text-align: center;
  padding-bottom: 25px;
}

.timer {
  font-weight: 900;
  font-size: 3em;
}

.color-white {
  color: rgb(255, 255, 255);
}

.color-green {
  color: rgb(153, 230, 153);
}

.color-red {
  color: rgb(230, 153, 153);
}
.sf-search {
  padding-top: 30px;
  padding-bottom: 30px;
}

.kl-requested-key {
  font-size: 56px;
}

input[type='text'] {
  border-top: 1px gray solid;
  border-bottom: 1px gray solid;
  border-left: 1px gray solid;
  border-right: 1px grey solid;

  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;

  outline: none;
  height: 55px;
  padding: 20px;

  font-size: 2em;
}

@media screen and (min-width: 1200px) {
  input[type='text'] {
    width: 91%;
  }
}

@media screen and (min-width: 781px) and (max-width: 1199px) {
  input[type='text'] {
    width: 85%;
  }
}

@media screen and (min-width: 540px) and (max-width: 780px) {
  input[type='text'] {
    width: 75%;
  }
}

/* For Tablets */
@media screen and (max-width: 539px) {
  input[type='text'] {
    width: 66%;
  }
}

.sf-btn-search {
  margin: 2px;
  height: 49px;
  border-top-right-radius: 13px;
  border-bottom-right-radius: 13px;

  font-size: 1.2em;
}

.input-group-btn {
  border-top: 1px gray solid;
  border-bottom: 1px gray solid;
  border-left: none;
  border-right: 1px gray solid;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;

  background-color: white;

  width: auto;
}

.color-grey {
  color: rgb(191, 191, 191);
}

.color-white {
  color: rgb(255, 255, 255);
}

.color-green {
  color: rgb(153, 230, 153);
}

.color-red {
  color: rgb(230, 153, 153);
}
.keyboard-tutor {
  height: 100%;
}  
